}
.login{
    /* text-align: center; */
    padding: 0 20%;
}
.title{
    text-align: center;
    margin: 30px 0;
}
.btn-login{
    margin-top: 10px;
    width: 100%;
    background-color: #4490f7;
    color: #fff;
    height: 40px;
    font-size: 18px;
}
p{
    display: block;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
}
.user-name,.password{
    width: 100%;
    height: 30px;
}
.forget-pwd{
    margin-top: 5px;
    border: none;
    color: #4490f7;
    background-color: white;
}
.forget-pwd-content{
    color:gray;
}
.forget-pwd-content b{
    color: red;
}
.login-error{
    color: red;
}
