.header{
    display:block;
}
.group{
    margin-bottom: 8px;
}
.divider{
    margin: 5px auto;
}
.math-header-above{
    font-size: 20px;
    font-weight: 500;
    /* font: normal 25px/50px Georgia,sans-serif; */
    background-color:chartreuse;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    height: 49px;
    line-height: 49px;
    padding-top: 5px;
    /* vertical-align:text-bottom;
    display: inline-block; */
}
.result-header{
    font-size: 13px;
    font-weight: 500;
}
.math-header{
    /* border: blueviolet 2px dashed; */
    padding: 10px 5px;
    float: left;
    width: 70%;
}
.result-area{
    overflow: auto;
    border: blueviolet 2px dashed;
    padding: 10px 5px;
    float: left;
    width: 30%;
    height: 194px;
}
.result-single{
    float: left;
    font-size: 15px;
    border: 2px;
    margin: 5px;
    height: 25px;
    width: 35px;
    text-align: center;
}
.fail{
    background-color:red;
}
.pass{
    background-color:gray;
}
.good{
    background-color: gold;
}
.outstanding{
    background-color: chartreuse;
}
.result-title{
    text-align: center;
    display: block;
    color: blue;
    font-weight: 600;
}
.title{
    font-size: 58px;
    text-align: center;
    color:brown;
}
.questions-count{
    width: 35px;
    color: red;
    font-weight: 600;
}
.font-size18{
    font-size: 18px;
}
.question{
    font-size: 18px;
}
.red{
    color: red;
    font-weight: 600;
}
.button-groups{
    margin-top: 10px;
}
.nodata-message{
    color: blue;
    font-size: 23px;
}
.blue{
    color: blue;
}
.main-area{
    height: 700px;
    overflow: auto;
    clear: both;
}
.answer-boder{
    border-style: double;
    border-color: blue;
}
.container{
